import React from 'react';
import './BlogCard.css';

interface BlogCardProps {
    title: string;
    description: string;
    author: {
        name: string;
        avatar?: string;
    };
    date: string;
    image: string;
    category: string;
    slug?: string;
}

const BlogCard: React.FC<BlogCardProps> = ({
    title,
    description,
    author,
    date,
    image,
    category,
    slug
}) => {
    const handleClick = () => {
        if (slug) {
            // Navigate to individual blog post page
            if (typeof window !== 'undefined') {
                window.location.href = `/blog/${slug}`;
            }
        }
    };

    return (
        <article className="blog-card" onClick={handleClick}>
            <div className="blog-card__image-wrapper">
                <img
                    src={image}
                    alt={title}
                    className="blog-card__image"
                    onError={(e) => {
                        // Fallback to a gradient background if image fails to load
                        e.currentTarget.style.display = 'none';
                    }}
                />
                <div className="blog-card__image-overlay"></div>
                <span className="blog-card__category">{category}</span>
            </div>

            <div className="blog-card__content">
                <h3 className="blog-card__title">{title}</h3>
                <p className="blog-card__description">{description}</p>

                <div className="blog-card__footer">
                    <div className="blog-card__author">
                        {author.avatar && (
                            <span className="blog-card__avatar">{author.avatar}</span>
                        )}
                        <span className="blog-card__author-name">{author.name}</span>
                    </div>
                    <span className="blog-card__date">{date}</span>
                </div>

                <button className="blog-card__cta">
                    <span>Read More</span>
                    <svg
                        className="blog-card__arrow"
                        width="20"
                        height="20"
                        viewBox="0 0 20 20"
                        fill="none"
                    >
                        <path
                            d="M7 4L13 10L7 16"
                            stroke="currentColor"
                            strokeWidth="2"
                            strokeLinecap="round"
                            strokeLinejoin="round"
                        />
                    </svg>
                </button>
            </div>
        </article>
    );
};

export default BlogCard;
