import React from 'react';
import Header from '../../componets/header/Header';
import Footer from '../../componets/footer/Footer';
import BlogCard from '../../componets/BlogCard/BlogCard';
import { getAllPosts } from '../../data/blogData';
import { getBlogContent } from '../../content/content_blog';
import { getContent } from '../../content/content';
import { useLanguage } from '../../context/LanguageContext';
import './blog.css';

const BlogPage: React.FC = () => {
    const { lang } = useLanguage();
    const allPosts = getAllPosts(lang);
    const content = getBlogContent(lang).blogPageContent;
    const globalContent = getContent(lang);

    return (
        <main className="blog-page">
            <Header content={globalContent?.pages?.globals?.section?.headerContent} />

            {/* Blog Hero Section */}
            <section className="blog-hero">
                <div className="blog-hero__overlay"></div>
                <div className="blog-hero__inner">
                    <span className="blog-hero__eyebrow">{content.eyebrow}</span>
                    <h1 className="blog-hero__title">
                        <span className="blog-hero__title-main">{content.titleMain}</span>
                        <span className="blog-hero__title-sub">{content.titleSub}</span>
                    </h1>
                    <p className="blog-hero__subtitle">
                        {content.subtitle}
                    </p>
                </div>
            </section>

            {/* Blog Grid Section */}
            <section className="blog-content">
                <div className="blog-content__inner">
                    <div className="blog-content__grid">
                        {allPosts.map((post, index) => (
                            <div
                                key={post.id}
                                className="blog-content__card"
                                style={{
                                    animation: `fadeInUp 0.6s ease-out ${0.08 * (index + 1)}s both`
                                }}
                            >
                                <BlogCard
                                    title={post.title}
                                    description={post.description}
                                    author={post.author}
                                    date={post.date}
                                    image={post.image}
                                    category={post.category}
                                    slug={post.slug}
                                />
                            </div>
                        ))}
                    </div>

                    {/* Pagination Note */}
                    <div className="blog-content__pagination">
                        <p className="blog-content__pagination-text">
                            {lang === "en" ? `Showing ${allPosts.length} articles` : `Hiển thị ${allPosts.length} bài viết`}
                        </p>
                    </div>
                </div>
            </section>

            <Footer content={globalContent?.pages?.globals?.section?.footerContent} />
        </main>
    );
};

export default BlogPage;
